/* Fonts
================================================== */
  // OpenSans
  @font-face {
  	font-family: 'open_sansregular';
      src: url('#{$fonts-path}/OpenSans/opensans-regular-webfont.eot');
      src: url('#{$fonts-path}/OpenSans/opensans-regular-webfont.eot?#iefix') format('embedded-opentype'),
           url('#{$fonts-path}/OpenSans/opensans-regular-webfont.woff') format('woff'),
           url('#{$fonts-path}/OpenSans/opensans-regular-webfont.ttf') format('truetype'),
           url('#{$fonts-path}/OpenSans/opensans-regular-webfont.svg#open_sansregular') format('svg');
    font-weight: normal;
  	font-style: normal;
    text-rendering: optimizeLegibility;
  }

  @font-face {
    font-family: 'open_sansitalic';
    src: url('#{$fonts-path}/OpenSans/opensans-italic-webfont.eot');
    src: url('#{$fonts-path}/OpenSans/opensans-italic-webfont.eot?#iefix') format('embedded-opentype'),
         url('#{$fonts-path}/OpenSans/opensans-italic-webfont.woff') format('woff'),
         url('#{$fonts-path}/OpenSans/opensans-italic-webfont.ttf') format('truetype'),
         url('#{$fonts-path}/OpenSans/opensans-italic-webfont.svg#open_sansitalic') format('svg');
    font-weight: normal;
    font-style: normal;
    text-rendering: optimizeLegibility;
  }

