/* Pages
================================================== */
  // Login + Create Account
  .sign {
    //background: $green;
    min-height: 100%;
    flex-direction: column;
    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      min-height: 100%;
      min-width: 100%;
      opacity: .45;
    }
    &.login {
      &:before {
        opacity: .9;
        background: url(#{$images-path}stock/calm-guy-min.jpg) no-repeat center bottom;
        background-size: cover;
      }
      .info {
        .title {
          h2 {
            text-shadow: 2px 1px rgba($black, .9);
          }
          p {
            text-shadow: 1px 1px rgba($black, .9);
          }
        }
      }
    }
    &.register {
      &:before {
        //background: url(#{$images-path}stock/cheering-girl-min.png) no-repeat center bottom;
      }
      .info {
        &:before {
          content: '';
          position: absolute;
          height: 100%;
          width: 100%;
          right: -100px;
          opacity: 0;
          right: 0;
          transition: opacity 1.25s ease-in;
        }

      }
    }
    .info {
      //background: $green;
      display: none;
      position: relative;
      width: 50%;
      .title {
        h1 {
          font-size: 2em;
          line-height: 120%;
        }
      }
      .list {
        //box-shadow: $box-shadow;
        //background: linear-gradient(0deg, rgba($white, 0.85), rgba($white, 0.99));
        margin-top: 40px;
        color: $white;
        img {
          width: 40px;
          height: 40px;
          float: left;
        }
        h3 {
          font-weight: bold;
          font-weight: 18px;
          margin-top: 20px;
        }
        ul {
          margin-left: 8px;
          li {
            margin: 0;
            background: none;
            border: none;
            position: relative;
            display: block;
            padding: 8px 0px 8px 32px;
            font-size: 18px;
            &:before {
              content: '';
              background: url(#{$images-path}icons/input-valid-icon.svg) no-repeat center bottom;
              background-size: cover;
              position: absolute;
              height: 16px;
              width: 16px;
              left: 4px;
              top: 8px;
            }
          }
        }
      }
    }
    .form {
      background:rgba($white, .9);
      box-shadow: $box-shadow;
      margin: 0;
      padding: 16px;
      .title {
        margin-bottom: 20px;
        line-height: 100%;
        small {
          display: block;
        }
      }
      .last {
        text-align: center;
        font-size: 16px;
        padding-top: 24px;
        small {
          margin: 8px 0 12px;
          display: block;
          font-size: 95%;
         }
        a {
          color: $cold;
        }
      }
    }
    .legals {
      background:rgba($white, .9);
      box-shadow: $box-shadow;
      margin: 0;
      padding: 16px;
      width: 100%;
    }
    &.login {
      background: $white;
    }
    @include breakpoint($ext-mobile) {
      .form, .form-extended {
        margin: 16px;
        width: calc(100% - 32px);
      }
    }
    @include breakpoint($tablet) {
      //background: $off-white;
      height: calc(100vh - 72px);
      flex-direction: row;
      .info {
        padding: 0 10px 0 40px;
        display: none;
        display: flex;
        .title {
          padding-top: 72px;
          color: $off-white;
          z-index: 100;
          position: relative;
          h1 {
            font-family: $openSansSemibold;
            font-size: 3em;;
          }
          p {
            margin: 4px 0;
            font-family: $openSansSemibold;
            font-size: 24px;
          }
        }
      }
      .form {
        box-shadow: none;
        margin: 0;
        width: 50%;
        padding: 16px 16px 48px 16px;
        .title {
          padding-top: 48px;
          margin-bottom: 36px;
          small {
            display: inline;
          }
        }
        .last {
          text-align: left;
        }
        &.form-extended {
          width: 100%;
        }
      }
      &.login, &.register {
        &:before {
          background: none;
        }
      }
      &.login {
        .info {
          background: url(#{$images-path}stock/calm-guy-min.jpg) no-repeat right bottom;
          background-size: cover;
        }
      }
      &.validate {
        .info {
          width: 30%;
        }
        .form {
          width: 70%;
        }
      }
    }
    @include breakpoint($ext-desktop) {
      .info {
        padding-left: 64px;
      }
      &.register {
        .info {
          &:before {
            content: '';
            //background: url(#{$images-path}stock/cheering-girl-min.png) no-repeat right bottom;
            background-size: 440px;
            opacity: .9;
            right: -80px;
            z-index: 20;
          }
        }
      }
    }
  }

  .new-index-bg {
    background: url(#{$images-path}home/bg.jpg) no-repeat center top;
    background-size: 400%;
    @include breakpoint($tablet) {
      background-size: cover;
    }
  }
  // Static Pages
  .new-index {
    flex-direction: column;
    .main-bg {
      background: url(#{$images-path}home/bg.jpg) no-repeat center top;
      background-size: 400%;
    }
    section {
      article {
        padding: 16px;
      }
      .btn {
        padding: 8px;
        border-radius: 24px;
        font-size: 16px;
        min-width: 240px;
        margin: 20px 0;
        font-weight: 700;
        &.primary {
          background: #ffd74a;
          border: solid 2px #dbb325;
          color: $black;
        }
        &.secondary {
          background: #e03655;
          border: solid 2px #bc1634;
          color: $off-white;
        }
      }
      .video {
        .video-responsive {
          width: 100%;
          height: auto;
          margin: 0 auto;
          text-align: center;
          }
        }
      }
      h2 {
        font-size: 1.75em;
        font-family: $openSansSemibold;
        line-height: 115%;
        margin: 16px 0 0 0;
      }
      &.hero {
        h1 {
          font-size: 1.75em;
          font-family: $openSansSemibold;
          line-height: 115%;
          margin: 24px 0 0 0;
          text-align: center;
          color: $white;
        }
        .options {
          margin-top: 36px;
          text-align: center;
          padding: 0;
          h3 {
            font-size: 1.25em;
            line-height: 125%;
            color: $off-white;
          }
        }
      }
      &.switch {
        text-align: center;
        article {
          padding-bottom: 16px;
        }
        h2 {
          color: $white;
        }
        span.strong {
          font-weight: 600;
        }
        .switch-control {
          margin-top: 20px;
          .control {
            > * {
              display: inline-block;
              margin: 0;
              vertical-align: middle;
            }
            .switch-label {
              width: 90px;
              margin: 0 4px;
            }
            .label-1, .label-2 {
              font-size: 1em;
              color: $white;
              width: calc(50% - 60px);
            }
          }
        }
      }
      &.working {
        text-align: center;
        article {
          padding-top: 16px;
        }
        .cards {
          margin: 0;
          li {
            padding: 20px;
            background-color: $off-white;
            margin: 0 0 20px;
            width: calc(100% - 0px);
            .circle {
              width: 120px;
              height: 120px;
              background: #38435d;
              border-radius: 60px;
              margin: 0 auto;
              text-align: center;
              padding-top: 20px;
            }
            h3 {
              margin-top: 32px;
              font-size: 1.25em;
              font-weight: 400;
              font-family: $openSansSemibold;
            }
            p {
              font-size: .925em;
              line-height: 130%;
            }
          }
        }
        .video {
          //background: #38435d;
          margin: 0;
        }
      }
      &.fundary {
        background: url(#{$images-path}home/white-bg.jpg) no-repeat center top;
        background-size: cover;
        min-height: 600px;
        text-align: center;
        .info {
          margin-top: 40px;
          p {
            font-size: 1em;
            line-height: 140%;
            margin-bottom: 20px;
            &.blue {
              color: #0277bd;
              margin-bottom: 20px;
            }
          }
        }
      }
      &.info {
        text-align: center;
        margin: 32px 0;
        h2 {
          color: $white;
        }
        article {
          padding: 48px 32px 16px;
          .text {
            margin-top: 40px;
          }
        }
        ul li,
        p {
          text-align: left;
          font-size: 1em;
          line-height: 140%;
          color: $white;
        }
        .number-list {
          counter-reset: my-badass-counter;
          li {
            display: block;
            position: relative;
            padding: 0 8px 0 40px;
            margin-bottom: 20px;
            &:before {
              content: counter(my-badass-counter);
              counter-increment: my-badass-counter;
              position: absolute;
              left: 0;
              top: 0;
              background: #38435d;
              font-size: 18px;
              line-height: 24px;
              font-weight: bold;
              border-radius: 50%;
              width: 24px;
              height: 24px;
              text-align: center;
            }
          }
        }
        p {
          margin-bottom: 20px;
          &.strong {
            font-weight: 700;
          }
        }
      }
      &.faq {
        background-color: $white;
        text-align: center;
        .question {
          margin-top: 40px;
        }
        .panel-default {
          border: none;
          background: none;
          text-align: left;
          &>.panel-heading {
            border-bottom: solid 1px $off-white;
            color: $cold;
            color: #0277bd;
            font-weight: 400;
            padding: 8px 40px 8px 16px;
            border-radius: 0px;
            position: relative;
            font-size: 1em;
            background-color: lighten($grey, 30);
            span {
              width: 20px;
              height: 20px;
              display: block;
              float: right;
              top: 10px;
              right: 20px;
              position: absolute;
              background: url(#{$images-path}icons/add-plus.png) no-repeat center center;
            }
            &[aria-expanded='true'] {
              border-left: solid 1px $soft-grey;
              border-right: solid 1px $soft-grey;
              border-bottom: none;
              span {
                background: url(#{$images-path}icons/minus-plus.png) no-repeat center center;
              }
            }
          }
          .panel-body {
            background-color: lighten($grey, 30);
            border: solid 1px $soft-grey;
            border-top: none;
            padding: 10px 30px;
          }
        }
      }
      &.contact {
        text-align: center;
        background-color: $off-white;
        .info {
          h4 {
            font-size: 1.25em;
            font-weight: 700;
            margin-bottom: 24px;
          }
          i {
            height: 20px;
            width: 28px;
            display: inline-block;
            margin: 0 8px 0 0;
            &.mail {
              background: url(#{$images-path}home/mail-icon.png) no-repeat center bottom;
              background-size: contain;            
            }
            &.phone {
              background: url(#{$images-path}home/phone-icon.png) no-repeat center bottom;
              background-size: contain;
            }
          }
          span {
            strong {
              color: #0277bd;
            }
          }  
        }
        .carousel-info {
          margin-top: 40px;
          h3 {
            text-transform: uppercase;
            font-weight: 700;
          }
          .carousel {
            position: relative;            
          }
          .carousel-inner {
            position: relative;
            width: 100%;
            overflow: hidden;
            .item {
              ul li {
                display: inline-block;
              }
            }
          }
          .carousel-indicators {
            position: relative;
            bottom: 0;
            margin: 20px -30%;
            li {
              width: 20px;
              height: 20px;
              margin: 4px 8px;
              border: solid 2px $grey;
              &.active {
                background-color: $grey;
              }
            }
          }
        }
        .legend {
          margin: 24px 0;
          p {
            font-size: .875em;
            text-align: left;
          }
        }
      }
    }

    @include breakpoint($ext-mobile) {
      section {
        &.switch {
          .switch-control {
            .control {
              .label-1, .label-2 {
                width: calc(50% - 80px);
              }
            }
          }
        }
      }
    }
    @include breakpoint($tablet) {
      .main-bg {
        background-size: cover;
      }
      section {
        article {
          padding: 48px 32px;
        }
        .btn {
          padding: 20px;
        }
        h2 {
          font-size: 2.25em;
          margin-top: 24px;
        }
        &.hero {
          h1 {
            font-size: 2.25em;
          }
          .options {
            padding: 0 24px;
            margin-top: 72px;
            h3 {
              font-size: 1.5em;
              line-height: 140%;
            }
          }
        }
        &.switch {
          .switch-control {
            margin-top: 36px;
            .control {
              > * {
                margin: 0 4px;
                width: auto;
              }
              .switch-label {
                margin: 0 16px;
              }
              .label-1, .label-2 {
                font-size: 1.5em;
              }
            }
          }
        }
        &.working {
          .cards {
            display: flex;
            margin-bottom: 20px;
            li {
              padding: 32px 24px;
              width: calc(33% - 16px);
              text-align: left;
              margin-right: 16px;
              .circle {
                width: 156px;
                height: 156px;
                border-radius: 78px;
                padding-top: 32px;
              }
              h3 {
                margin-top: 20px;
                font-size: 1.5em;
              }
              p {
                font-size: 1.25em;
              }
              &:first-child,
              &:last-child {
                width: calc(34% - 0px);
              }
              &:last-child {
                margin-right: 0;
              }
            }
          }
          .video {
            width: calc(100% - 0px);
            .embed-responsive {
              position: relative;
              min-width: 420px;
              height: 315px;
            }
            .embed-responsive-16by9 {
              padding-bottom: 0;
            }
          }
        }
        &.fundary {
          .info {
            text-align: left;
          }
        }
        &.info {
          ul li, p {
            font-size: 1.375em;
          }
        }
        &.faq {
          .panel-default {
            &>.panel-heading {
              padding: 16px 44px 16px 16px;
              font-size: 1.25em;
              span {
                top: 20px;
              }
            }
          }
        }
        &.contact {
          .info {
          }
        }
      }
    }
    @include breakpoint($desktop) {
      section {
        &.hero {
          .options {
            padding: 0 40px;
          }
        }
        &.fundary {
          .info {
            p {
              font-size: 1.5em;
              margin-bottom: 0;
            }
            .video {
              margin: 0 8px;
              .embed-responsive {
                position: relative;
                min-width: 400px;
                height: 315px;
              }
              .embed-responsive-16by9 {
                padding-bottom: 0;
              }
            }
          }
        }
        &.contact {
          .info {
            h4 {
              font-size: 2em;
              &.solo {
                margin-top: 36px;
              }
            }
            i {
              height: 28px;
              width: 36px;
              display: block;
              margin: 0 auto 8px;
            }
          }
        }
      }
    }
    @include breakpoint($ext-desktop) {
      section {
        h2 {
          font-size: 3em;
        }
        &.hero {
          h1 {
            font-size: 3em;
          }
        }
        &.contact {
          .info {
            h4 {
              &.solo {
                margin-top: 0px;
              }
            }
            i {
              display: inline-block;
              margin: 0 8px 0;
            }
          }
        }
      }
    }


  .index1 {
    flex-direction: column;
    section {
      display: flex;
      article {
        padding: 48px 32px;
        h3 {
          font-size: 2.125em;
          margin-bottom: 32px;
        }
      }
      &.hero {
        flex-direction: column;
      }
      .info, .form {
        margin: 0;
        padding: 16px;
        position: relative;
        width: 100%;
      }
      .info {
        color: $white;
        //background: $black;
        &:before {
          content: '';
          opacity: .8;
          //background: url(#{$images-path}stock/people-girls-min.jpg) no-repeat center bottom;
          background-size: cover;
          position: absolute;
          bottom: 0;
          left: 0;
          min-height: 100%;
          min-width: 100%;
        }
        h2 {
          font-size: 2em;
          font-weight: bold;
          font-family: $openSansSemibold;
          line-height: 1.5em;
          margin: 20px 0 20px;
        }
        ul {
          li {
            font-size: 1.25em;
            padding-left: 40px;
            line-height: 1.25em;
            margin-bottom: 12px;
            font-family: $openSansSemibold;
            position: relative;
            &:before {
              content: '';
              background: url(#{$images-path}icons/input-valid-icon.svg) no-repeat center bottom;
              background-size: cover;
              position: absolute;
              height: 16px;
              width: 16px;
              left: 8px;
              top: 4px;
            }
          }
        }
        .btn {
          margin: 40px 20px;
        }
      }
      .form {
        display: block;
        .title {
          font-size: 1.5em;
          line-height: 2em;
          margin-bottom: 20px;
          p {
            font-size: 80%;
          }
        }
        p.last {
          margin: 10px;
          display: inline-block;
          i {
            display: inline-block;
          }
          span {
            margin: 0 0 0 10px;
            font-weight: bold;
            font-size: 14px;
            line-height: 1.5em;
            float: right;
          }
        }
      }
      .how-to-work {
        background: $green;
        width: 100%;
        color: $white;
        .steps {
          li {
            line-height: 1.75em;
            margin-bottom: 20px;
            font-size: 1.125em;
            span {
              width: 24px;
              height: 24px;;
              background-color: $black;
              color: $white;
              float: left;
              border-radius: 12px;
              text-align: center;
              margin-right: 20px;
              line-height: 1.7em;
            }
          }
          p {
            padding: 0 0 10px 48px;
          }
        }
      }
      .offer {
        p {
          font-size: 1.25em;
          line-height: 1.5em;
          &.first {
            color: $dark-green;
            font-weight: bold;
            margin-bottom: 16px;
          }
        }
      }
      .faq {
        width: 100%;
        background-color: $white;
        .panel-default {
          border: none;
          background: none;
          &>.panel-heading {
            border-bottom: solid 1px $off-white;
            color: $cold;
            padding: 20px 30px 10px;
            border-radius: 0px;
            position: relative;
            background-color: lighten($grey, 30);
            span {
              width: 20px;
              height: 20px;
              display: block;
              float: right;
              top: 20px;
              right: 20px;
              position: absolute;
              background: url(#{$images-path}icons/add-plus.png) no-repeat center center;
            }
            &[aria-expanded='true'] {
              border-left: solid 1px $soft-grey;
              border-right: solid 1px $soft-grey;
              border-bottom: none;
              span {
                background: url(#{$images-path}icons/minus-plus.png) no-repeat center center;
              }
            }
          }
          .panel-body {
            background-color: lighten($grey, 30);
            border: solid 1px $soft-grey;
            border-top: none;
            padding: 10px 30px;
          }
        }
      }
    }
    @include breakpoint($ext-mobile) {
      section .form {
        margin: 16px;
        width: calc(100% - 32px);
      }
    }
    @include breakpoint($tablet) {
      section {
        article {
          padding: 64px 48px;
        }
        &.hero {
          flex-direction: row;
        }
        .info {
          display: block;
          width: 50%;
          }
        .form {
          width: 50%;
          box-shadow: none;
        }
        .info {
          padding-top: 40px;
          h2 {
            margin-top: 60px;
          }
        }
        .how-to-work .steps {
          width: 80%;
          margin: 0 auto;
        }
        .offer {
          width: 80%;
          margin: 0 auto;
        }
      }
    }
  }
  .index {
    background: $off-white;
    width: 100%;
    margin: 0;
    .hero-container {
      overflow: hidden;
      position: relative;
      .ribbon-hero,
      .info-hero {
        position: relative;
        transition: transform .8s;
        width: 100%;
        height: 100%;
        top: 0;
        z-index: 10;
        .hero-tilt {
          width: 90%;
          margin: 0 auto;
          padding: 24px 0;
          color: $white;
          text-align: center;
          position: relative;
          z-index: 40;
          .round-filled-icon {
            display: inline-block;
            background: #fff;
            font-size: 36px;
            color: #009cde;
            border-radius: 40px;
            width: 40px;
            height: 40px;
            line-height: 38px;
          }
        }
      }
      .ribbon-hero {
        background: $green;
        h2 {
          font-size: 2em;
          line-height: 1.25em;
        }
      }
      .info-hero {
        h1 {
          line-height: 1.5em;
          margin-bottom: 20px;
        }
        .hero-twin-items {
          text-align: left;
          margin: 0;
        }
        &:before {
          content: '';
          transition: opacity .8s;
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
    p {
      line-height: 24px;
    }
    h3 {
      font-size: 1.75em;
    }
    .contact .panel {
      padding-top: 24px;
      margin-top: -10px;
      a {
        color: $cold;
      }
    }
    .intro {
      padding: 30px 15px;
      .title {
        margin: 0 auto 40px;
        h3 {
          font-family: $openSansLight;
          margin-bottom: 12px;
        }
        br {
          line-height: 240%;
        }
      }
      .steps {
        margin: 24px auto 0;
        li {
          margin-bottom: 24px;
          p {
            text-align: left;
          }
        }
      }
    }
    .working {
      background: $white;
      border-top: 1px solid $soft-grey;
      text-align: center;
      padding: 48px 15px;
      .title {
        margin: 20px auto 40px;
        border-bottom: 1px solid $cold;
        > li {
          display: inline-block;
          padding: 0px 16px 0;
          float: none;
          &.active {
            a {
              color: $cold;
              border-bottom: 2px solid $cold;
            }
          }
        }
      }
      .steps {
        margin: 0 auto;
        span {
          display: none;
        }
      }
    }
    .faq {
      padding: 30px 15px;
      .panel-default {
        border: none;
        background: none;
        &>.panel-heading {
          border: none;
          border-top: solid 1px $grey;
          border-bottom: solid 1px $grey;
          color: $cold;
          padding: 20px 30px 10px;
          box-shadow: none;
          position: relative;
          span {
            width: 20px;
            height: 20px;
            display: block;
            float: right;
            top: 20px;
            right: 20px;
            position: absolute;
            background: url(#{$images-path}icons/add-plus.png) no-repeat center center;
          }
          &[aria-expanded='true'] {
            border-bottom-color: transparent;
            span {
              background: url(#{$images-path}icons/minus-plus.png) no-repeat center center;
            }
          }
        }
        .panel-body {
          padding: 0 30px 10px;
          p {

          }
        }
      }
    }
    @include breakpoint($tablet) {
      .hero-container {
        height: 400px;
        .ribbon-hero,
        .info-hero {
          .hero-tilt {
            width: 80%;
            padding-top: 96px;
          }
        }
        .ribbon-hero {
          box-shadow: 1px 0 10px rgba($black, .7);
          z-index: 20;
          .hero-tilt {
            padding-top: 140px;
          }
        }
        .info-hero {
          position: absolute;
          h1 {
            margin-right: 40px;
          }
          .hero-twin-items {
            position: absolute;
            bottom: 10px;
            .hero-twin-item {
              float: left;
              width: 46%;
              margin: 0 0 0 4%;
            }
          }
        }
      }
      .intro {
        .title {
          width: 80%;
        }
        .steps {
          width: 80%;
        }
      }
      .working {
        .steps {
          width: 80%;
          li {
            float: left;
            width: 30%;
            margin-left: 1%;
            margin-right: 1%;
            text-align: left;
          }
        }
      }
    }
  }
  .index {
    .hero-container {
      .info-hero {
        background: url(#{$images-path}stock/people-min.jpg) no-repeat center top;
        background-size: cover;
        &:before {
          background: linear-gradient(0deg, rgba($dark-cold, .77), rgba($green, .33));
        }
      }
    }
    @include breakpoint($tablet) {
      .hero-container {
        .ribbon-hero {
          transform: skewX(-25deg);
          width: 50%;
          left: -15%;
          .hero-tilt {
            transform: skewX(25deg);
            left: 10%;
          }
        }
        .info-hero {
          width: 80%;
          right: -10%;
          transform: translateX(-10%);
          .hero-tilt {
            margin-right: 32px;
            text-align: right;
            min-height: 100%;
          }
        }
      }
    }
  }


  // My Content Internal Pages
  .my-content {
    flex-direction: column;
    min-height: 100%;
    .form-data {
      .account {
        padding: 12px 0;
        margin-bottom: 12px;
        position: relative;
        &.image {
          border-bottom: solid 1px $soft-grey;
        }
        .form-group {
          &:last-child {
            margin-bottom: 0;
          }
        }
        p {
          margin-top: 10px;
          color: $dark-grey;
        }
        &.disabled {
          &:before {
            content: '';
            background: $white;
            opacity: .6;
            width: 100%;
            height: 100%;
            display: block;
            position: absolute;
            z-index: 50;
          }
        }
      }
      .image-profile {
        clip-path: circle(32px at center);
        width: 72px;
        height: 72px;
        border-radius: 100px;
        border: solid 4px lighten($grey, 20);
        float: left;
        margin-right: 20px;
        img {
          height: 100%;
        }
      }
    }
    .balance {
      background: lighten($green, 40);
      border: solid 1px $green;
      padding: 16px;
      border-radius: 4px;
      p, a {
        display: inline-blocK;
        margin-right: 10px;
      }
    }
    @include breakpoint($tablet) {
      flex-direction: row;
      .form-data {
        .form-inline {
          .btn-sm {
            margin-left: 8px;
          }
        }
      }
      .balance {
        position: absolute;
        z-index: 10;
        right: 48px;
        top: 24px;
        text-align: center;
        p, a {
          display: block;
          margin-right: 0;
        }
      }
    }
  }


  // Credit
  .credit {
    padding: 0 24px;
    .credit-legend {
      color: $green;
      margin: 0 0 8px 0px;
      padding: 7px 0;
      font-size: 18px;
      font-weight: 600;
      border-radius: 5px;
      display: inline-block;
    }
    .summary {
      margin: 36px 0 18px;
      border-radius: 7px;
      background-color: lighten($green, 45);
      border: solid 2px $dark-green;
      li {
        flex-grow: 1;
        text-align: center;
        padding: 8px 4px;
        display: block;
        position: relative;
        span {
          font-size: 13px;
          text-transform: uppercase;
          color: $grey;
          display: block;
          & + span {
            display: block;
            color: $green;
            font-size: 20px;
          }
          &.operator {
            display: block;
            position: absolute;
            top: 14px;
            color: $grey;
            right: -8px;
            font-size: 32px;
            margin-top: -8px;
            z-index: 5;
          }
        }
      }
    }
    @include breakpoint($tablet) {

    }
  }


  // Legal
  .legal {
    article {
      h3 {
        margin-top: 32px;
        margin-bottom: 8px;
        font-weight: 700;
      }
      ol {
        list-style-position: outside;
        padding-left: 24px;
        ol {
          list-style-type: lower-roman;
          &.latin {
            list-style-type: lower-latin;
          }
          &.roman {
            list-style-type: lower-latin;
          }
        }
      }
      p {
        line-height: 160%;
        margin-bottom: 16px;
      }
      strong.caps {
        text-transform: capitalize;
        font-size: 120%;
        font-variant: small-caps;
      }
    }
    .secondary-module {
      text-align: right;
      margin: 20px;
      .sidebar-nav {
        background: $off-white;
        overflow: hidden;
        margin: 0 0 20px;
        border-radius: 5px;
        box-shadow: 0 1px 2px rgba($black, .2);
        li {
          border-bottom: 1px solid $soft-grey;
          color: $dark-grey;
          position: relative;
          transition: background-color 0.3s ease, color 0.3s ease;
          &:first-child {
            border-radius: 5px 5px 0 0;
          }
          &.current a {
            background: $white;
            color: $grey;
            cursor: not-allowed;
            opacity: 0.6;
            text-decoration: none;
          }
        }
        a {
          display: block;
          font-weight: 500;
          color: inherit;
          font-size: 14px;
          padding: 15px 20px;
          transition: padding 0.3s ease;
        }
      }
      .contact {
        h3 {
          margin: 0 0 15px 0;
          font-size: 18px;
          color: $dark-grey;
        }
        address {
          font-size: 14px;
          color: $dark-grey;
          line-height: 1.6;
          margin-bottom: 15px;
        }
      }
    }
  }


