/* Vendor
================================================== */
  @import 'vendor/breakpoint';


/* Base UI
================================================== */
  //helpers
  @import 'blockbliss/base/_variables';                  // Variables
  @import 'blockbliss/base/_reset';                      // Reset
  @import 'blockbliss/base/_fonts';                      // Fonts


/* Global
=================================================== */
  html, body {
    height:100%;
    background: $off-white;
    box-sizing:border-box;
  }
  *{
    &:before,&:after{
      box-sizing:border-box;
    }
  }
  body {
    color: $dark-grey;
    font-family: $ff-primary;
    font-size: $body-font-size;
    line-height: $base-line-height;
    
  }
  .wrapper {
    font-size: 1em;
    line-height: 1.25;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    max-width: 100vw;
    min-height: 100%;
	  flex-flow: column nowrap;
    flex: 0 1 auto;
    &.wrapper-landing {
      .main-content {
        height: 100%;
      }
    }
    @include breakpoint($tablet) {
      &.wrapper-landing {
        .main-content {
          height: 100%;
        }
        & + #footer {
          position: relative;
        }
      }
    }
  }
  .main-content {
    position: relative;
    height: calc(100vh - 54px);
    background-color: $off-white;
    @include breakpoint($tablet) {
      height: calc(100vh - 72px);
      min-height: 100%;
    }
    @include breakpoint($desktop-l) {
      margin: 0 auto;
      width: 1440px;
      > .flexbox {
        border-right: solid 1px $dark-green;
        border-left: solid 1px $grey;
      }
    }
    &.landing {
      height: 100%;
    }
  }
  .flexbox,
  .flexbox-item {
    display: flex;
  }
  .flexbox-item {
    flex-grow: 0;
    align-items: stretch;
    &.spaced {
      justify-content: space-between;
    }
  }


/* UI General
================================================== */
  //components
  @import 'blockbliss/components/mixins';            // Typography
  @import 'blockbliss/components/typography';            // Typography
  @import "blockbliss/components/fonts-icons";           // Fonts
  @import 'blockbliss/components/buttons';               // Buttons
  @import 'blockbliss/components/forms';                 // Forms
  @import 'blockbliss/components/datatable';             // Datatable
  @import 'blockbliss/components/bg';                    // Backgrounds
/* UI Modules
================================================== */
  @import 'blockbliss/components/atomic';               // Modules
  @import 'blockbliss/components/header';               // Header & Footer
  @import 'blockbliss/components/pages';                // Pages


