/* Modules
================================================== */
  // Atoms
  .container-fluid.no-padding {
    padding-left: 0;
    padding-right: 0;
    .row {
      margin-left: 0;
      margin-right: 0;
      [class^='col-'] {
        padding-left: 0;
        padding-right: 0;
      }
    }
    @include breakpoint($tablet) {
      padding-left: 15px;
      padding-right: 15px;
      .row {
        margin-left: -15px;
        margin-right: -15px;
        [class^='col'] {
          padding-left: 15px;
          padding-right: 15px;
        }
      }
    }
  }
  .add-space {
    margin-top: 20px;
    &.s2x {
      margin-top: 40px;
    }
  }
  ul {
    list-style: none;
  }
  .form-group.bank {
    .form-control {
      margin: 0 auto;
    }
    .link {

    }
  }
  .bg-white {
    background-color: rgba($white, .66);
    border-radius: 8px;
  }

  // Molecles
  .form {
    width: 100%;
    position: relative;
    padding-bottom: 56px;
    @include breakpoint($tablet) {
      box-shadow: -1px 0 4px 0 rgba($black, .2);
      //background: rgba($white, .3);
      width: calc(100% - 240px);
      padding-bottom: 48px;
      &.form-extended {
        width: 100%;
      }
    }
  }
  .switch-label {
    position: relative;
    display: inline-block;
    width: 96px;
    height: 44px;
    border: solid 2px $white;
    border-radius: 22px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    input {
      display: none;
      &:checked + .slider {
        background-color: lighten(rgba(#e03655, .33), 30)
      }
      &:checked + .slider:before {
        -webkit-transform: translateX(42px);
        -ms-transform: translateX(42px);
        transform: translateX(42px);
        background-color: #e03655;
      }
      &:focus + .slider {
        box-shadow: 0 0 1px #2196F3;
      }
    }
    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: lighten(rgba(#ffd74a, .33), 30);
      -webkit-transition: .4s;
      transition: .4s;
      &.round {
        border-radius: 24px;
      }
      &:before {
        position: absolute;
        content: "";
        height: 36px;
        width: 36px;
        left: 4px;
        top: 2px;
        background-color: #ffd74a;
        -webkit-transition: .4s;
        transition: .4s;
      }
      &.round:before {
        border-radius: 50%;
      }
    }
  }
  // Btn
  .btn-group {
    img {
      display: inline;
      margin: 0 4px;
    }
  }
  i.arrow {
    border: solid $dark-cold;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    margin-right: 6px;
    &.right {
      transform: rotate(-45deg);
    }
    &.left {
      transform: rotate(135deg);
    }
    &.up {
      transform: rotate(-135deg);
    }
    &.down {
      transform: rotate(45deg);
    }
  }
  .form-group {
    .btn-primary {
      display: block;
      width: 80%;
      max-width: 360px;
      margin: 8px auto 0 auto;
    }
    @include breakpoint($tablet) {
      .btn-primary {
        width: 60%;
      }
    }
    @include breakpoint($tablet) {
      .btn-primary {
        display: inline-block;
        width: auto;
      }
    }
  }
  // List
  .single-list {
    padding: 4px 16px;
    @include breakpoint($tablet) {
      padding: 4px 24px;
    }
  }
  .list {
    color: $grey;
    padding: 4px;
    p {
      font-size: 18px;
    }
    li {
      font-size: 14px;
      line-height: 22px;
      padding: 8px 4px;
      margin-bottom: 4px;
      background: $white;
      border: solid 1px $grey;
      border-radius: 4px;
      margin: 0 8px;
      flex-grow: 1;
      justify-content: center;
      span {
        display: block;
        &.number {
          font-size: 18px;
          font-weight: 700;
        }
      }
    }
    &.single-list {
      padding: 4px 16px;
    }
    @include breakpoint($tablet) {
      padding: 0;
      max-width: 360px;
      &.single-list {
        max-width: initial;
        padding: 4px 24px;
      }
      li {
        margin: 0 8px;
        &:first-child {
          margin-left: 0;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  .bootstrap-select {
    .dropdown-menu {
      &.inner {
        li {
          padding: 8px;
        }
      }
    }
    &:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
      width: 100% !important;
    }
  }
  // Info
  .callout {
    margin: 20px 0;
    padding: 20px;
    border-left: 3px solid $off-white;
    h4 {
      margin-top: 0;
      margin-bottom: 5px;
    }
    &.callout-info {
      background-color: lighten($light-grey, 2);
      border-color: lighten($green, 4);
    }
    &.callout-warning {
      background-color: lighten($light-grey, 2);
      border-color: lighten($deep-warm, 4);
    }
  }
  // Upload Image
  .file-input {
    border-radius: 5px;
    border: 1px solid $soft-grey;
    padding: 16px;
    width: 100%;
    margin-bottom: 16px;
    .file-preview {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
    img {
      vertical-align: middle;
      border-style: none;
      margin-bottom: 16px;
    }
    .nav-tabs {
      margin-top: 20px;
      border: none;
    }
  }
  .btn.file {
    position: relative;
    overflow: hidden;
    padding: 6px 24px;
    input {
      position: absolute;
      font-size: 22px;
      opacity: 0;
      right: 0;
      top: 0;
    }
  }
  .avatar-hint {
    margin: 16px 0 8px;
  }
  .tooltip.in {
    opacity: .98;
  }
  .tooltip-inner {
    max-width: 280px;
    border: solid 1px $grey;
    background-color: $off-white;
    color: $black;
    font-size: 1.15em;
    line-height: 140%;
    text-align: left;
    padding: 8px 12px;
  }


  // Components
  // Bars
  .main-bar {
    min-height: 0;
    .title {
      position: absolute;
      z-index: 150;
      left: 44px;
      top: -48px;
      font-size: 1.5em;
      color: $white;
      margin: 0;
      float: left;
    }
    .btn-group {
      position: absolute;
      z-index: 150;
      right: 8px;
      top: -46px;
      .btn {
        padding: 8px;
        background: none;
        border-color: transparent;
        span {
          display: none;
        }
      }
    }
    @include breakpoint($tablet) {
      .title {
        position: initial;
        color: initial;
        font-size: 2em;
        padding: 0 16px;
      }
      .btn-group {
        position: initial;
        float: right;
        margin: 16px 16px 0 0;
        .btn {
          padding: 8px 16px;
          background-color: $white;
          border-color: $grey;
          img {
            display: none;
          }
          span {
            display: inline-block;
          }
        }
      }
    }
  }
  .tab-nav {
    position: fixed;
    z-index: 20;
    width: 100%;
    bottom: 0;
    background: $white;
    transition: padding .85s ease-in;
    box-shadow: 1px 0 6px 0 rgba($black, .2);
    ul {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin: 0;
      li {
        width: 25%;
        a {
          display: block;
          padding: 12px 8px;
          text-align: center;
          position: relative;
          span {
            display: none;
            font-size: 11px;
          }
          i {
            width: 24px;
            height: 24px;
            border: solid 0px $deep-warm;
            display: inline-block;
            &:before {
              font-size: 20px;
            }
          }
          &.active,
          &:active {
            padding: 9px 8px 6px;
            span {
              display: block;
            }
            i {
              color: $deep-warm;
            }
          }
          &.active,
          &:hover,
          &:active {
            text-decoration: none;
            &:before {
              border-bottom: solid 4px $green;
              width: 100%;
              height: 4px;
              position: absolute;
              bottom: 0;
              left: 0;
            }
          }
        }
      }
      &.empty {
        display: none;
      }
    }
    @include breakpoint($tablet) {
      position: relative;
      background: none;
      margin: 16px;
      box-shadow: none;
      width: 240px;
      ul {
        flex-direction: column;
        align-items: flex-start;
        li {
          width: 100%;
          margin-bottom: 12px;
          a {
            min-height: 56px;
            padding: 16px 0px 16px 16px;
            i, span {
              float: left;
            }
            span {
              padding-left: 16px;
              display: inline-block;
              line-height: 24px;
              font-size: 16px;
              margin: 0;
            }
            &.active,
            &:hover,
            &:active {
              padding: 16px 0px 16px 16px;
              background-color: lighten($green, 45);
              border-radius: 2px 0 0 2px;
            }
            &.active,
            &:active {
              &:before {
                border-bottom-width: 2px;
              }
            }
          }
        }
        &.empty {
          display: flex;
          justify-content: center;
          height: calc(100% - 200px);
          li {
            display: flex;
            justify-content: center;
          }
        }
      }
    }
  }
  .btn-bar {
    margin-bottom: 16px;
  }

  // Container
  .white-block {
    padding: 12px 0;
    .title {
      margin-bottom: 24px;
    }
    h2 {
      margin: 0;
      &.underline {
        border-bottom: solid 1px $soft-grey;
      }
    }
    .subtitle {
      padding: 16px 0 8px;
      border-bottom: solid 1px $deep-warm;
    }
    &.no-padding {
      padding: 0;
    }
    &.no-bg {
      background: none;
      border-color: transparent;
    }
    @include breakpoint($tablet) {
      padding-left: 12px;
      padding-right: 12px;
      border: solid 1px $soft-grey;
      background:rgba($white, .88);
      margin-left: 16px;
      margin-right: 16px;
      min-height: 70vh;
      &.no-padding {
        padding: 12px;
      }
    }
  }
  .green-block {
    background: url(#{$images-path}ui/bg-poly.jpg) no-repeat center bottom $deep-warm;
    background-size: cover;
    min-height: 160px;
    padding-top: 20px;
    text-align: center;
    &.image-block {
      margin-bottom: 80px;
    }
    &.bg-none {
      background: none;
    }
    .image-profile {
      width: 120px;
      margin: 40px auto 0;
      border: solid 6px rgba($grey, .3);
      border-radius: 100%;
      &.no-border {
        border-color: transparent;
      }
    }
    @include breakpoint($tablet) {
      background: none;
    }
  }
  .modal {
    &:before {
      content: '';
      display: inline-block;
    }
    .modal-header {
      background-color: $dark-green;
      color: $off-white;
      .close {
        opacity: .8;
        color: $off-white;
        text-shadow: 0 1px 0 $grey;
      }
    }
    .modal-footer {
      text-align: center;
      .avatar-hint {
        margin: 0;
      }
    }
    .panel {
      box-shadow: none;
    }
  }
  .inner-white-block {
    background: $white;
    box-shadow: 0 0 4px 0 rgba($black, .4);
    margin: -24px 24px 24px;
    padding: 24px;
  }

  // Docs
  .info-box {
    border: solid 1px rgba($green, .2);
    border-left: solid 3px $green;
    background-color: rgba($green, .04);
    margin-bottom: 16px;
    padding: 8px 0 24px 32px;
    border-radius: 3px;
    .items-box {
      padding: 0 24px;
    }
    li {
      font-size: 14px;
      line-height: 22px;
      padding: 8px 4px;
      margin-bottom: 4px;
      i.download {
        background: url(#{$images-path}icons/download-icon.svg) no-repeat center bottom transparent;
        background-size: cover;
        width: 24px;
        height: 24px;
        display: block;
        float: right;
        right: 16px;
        bottom: 0;
        position: relative;
      }
      &.required:before {
        content: '*';
        position: relative;
        margin-right: 8px;
        display: inline;
        font-size: 12px;
      }
    }
    .empty-state {
      text-align: center;
      padding: 24px;
      img {
        margin-bottom: 20px;
      }
    }
    &.files {
      border: dotted 2px rgba($green, .6);
      padding: 8px 0;

    }
    @include breakpoint($tablet) {
      &.files {
        padding: 16px;
      }
    }
  }
  .item-box{
    border: solid 1px transparent;
    border-bottom: solid 1px $grey;
    margin-bottom: 16px;
    padding-bottom: 4px;
    &:last-child {
      border-bottom-color: transparent;
      margin-bottom: 0;
    }
    i {
      height: 32px;
      width: 24px;
      display: inline-block;
    }
    span {
      display: inline-block;
      line-height: 32px;
      margin-left: 8px;
      color: $grey;
      font-size: 13px;
      text-align: left;
      width: calc(100% - 48px);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &[type=pdf] {
      i {
        background: url(#{$images-path}icons/item-pdf.png) no-repeat center center;
        background-size: contain;
      }
    }
    &[type=jpg] {
      i {
        background: url(#{$images-path}icons/item-jpg.png) no-repeat center center;
        background-size: contain;
      }
    }
  }
  .form-list {
    .title {
      margin-bottom: 0 !important;
      h2 {
        float: left;
        width: calc(100% - 40px);
      }
      a.download {
        background: url(#{$images-path}icons/download-icon.svg) no-repeat center center;
        background-size: cover;
        float: right;
        width: 32px;
        height: 32px;
        border: solid 1px $black;
        border-radius: 3px;
      }
    }
    .form-group {
      &.highlight {
        border-bottom: solid 2px $green;
        label {
          margin: 0;
        }
      }
      label {
        text-align: center;
        color: lighten($grey, 10);
        &:last-child {
          color: $black;
          padding: 8px;
          border-bottom: solid 1px darken($off-white, 5);
        }
      }
    }
    @include breakpoint($tablet) {
      .form-group {
        label {
          padding: 8px 0;
          text-align: right;
          &:last-child {
            text-align: left;
          }
        }
      }
    }
  }

  // Grids
  .nav-tabs {
    margin-bottom: 0;
    background: none;
    > li {
      margin-bottom: 0;
      width: auto;
      flex-grow: 1;
      text-align: center;
      a {
        background: transparent;
        border-color: transparent;
        color: $black;
        padding: 16px 12px;
        border: none;
        border-bottom: solid 2px transparent;
        &:focus,
        &:hover {
          background-color: $light-grey;
        }
      }
      &.active {
        a,
        a:focus,
        a:hover {
          border: none;
          border-bottom: solid 2px $deep-warm;
          background: none;
        }
      }
    }
  }
  .data-grid {
    margin-bottom: 4px;
    .data-grid-heading {
      flex-flow: row wrap;
      padding: 16px 0 16px 8px;
      background: $white;
      border-bottom: solid 1px $grey;
      .person {
        padding-right: 8px;
        max-width: 80px;
        position: relative;
        .status {
          display: none;
          &[type=up-to-date] {
            & + i.icon {
              &:before {
                content: '';
                position: absolute;
                width: 24px;
                height: 24px;
                border-radius: 12px;
                border: solid 4px $white;
                z-index: 10;
                right: 8px;
                top: -8px;
                background-color: lighten($green, 5);
              }
            }
          }
          &[type=overdue] {
            & + i.icon {
              &:before {
                content: '';
                position: absolute;
                width: 24px;
                height: 24px;
                border-radius: 12px;
                border: solid 4px $white;
                z-index: 10;
                right: 8px;
                top: -8px;
                background-color: lighten($warm, 0);
              }
            }
          }
          &[type=failed] {
            & + i.icon {
              &:before {
                content: '';
                position: absolute;
                width: 24px;
                height: 24px;
                border-radius: 12px;
                border: solid 4px $white;
                z-index: 10;
                right: 8px;
                top: -8px;
                background-color: darken(red, 10);
              }
            }
          }
        }
        img {
          border-radius: 100%;
          max-width: 48px;
          max-height: 48px;
          box-shadow: 0 4px 6px rgba($black, .10);
        }
      }
      .data {
        width: calc(100% - 88px);
        position: relative;
        flex-flow: column;
        align-items: flex-start;
        span {
          text-align: left;
          display: block;
          &.name {
            font-weight: 800;
            margin-bottom: 4px;
            font-size: 120%;
          }
          &.xtra {
            font-weight: 800;
            margin-top: 4px;
            font-size: 140%;
            display: block;
          }
          &.more-icon {
            background: url(#{$images-path}icons/arrow-right-icon.svg) no-repeat center center;
            background-size: cover;
            width: 32px;
            height: 32px;
            position: absolute;
            top: 8px;
            right: -16px;
          }
        }
        .btn-group {
          margin: 8px 0 0 0;
          min-width: 160px;
          &.standalone {
            min-width: 80px;
          }
          .btn {
            margin: 0 2px;
          }
        }
      }
      &[aria-expanded='true'] {
        border-color: transparent;
        .data {
          span.more-icon {
            background: url(#{$images-path}icons/arrow-up-icon.svg) no-repeat center center;
            background-size: cover;
            border-radius: 3px;
            border: solid 1px rgba($green, .5);
          }
        }
      }
      &.user {
       padding: 8px 4px;
       margin-bottom: 10px;
       border: solid 1px $grey;
       border-radius: 7px;
       align-items: center;
       .person {
         img {
          max-width: 32px;
          max-height: 32px;
         }
       }
      }
    }
    .data-grid-body {
      background-color: $off-white;
      padding: 16px;
      box-shadow: 0 2px 4px -1px rgba($black, .3);
      border-radius: 3px;
    }
    @include breakpoint($desktop) {
      .data-grid-heading {
        .data {
          padding-right: 8px;
          flex-flow: row;
          .concept {
            flex-grow: 2;
            padding: 0 8px;
          }
          span {
            &.name {
              font-size: 100%;
              min-width: 200px;
              width: 200px;
            }
            &.xtra {
              margin-top: 0;
              font-size: 100%;
              min-width: 120px;
              width: 120px;
            }
          }
        }
      }
    }
  }

  // Slider
  .rzslider {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 4px;
    margin: 48px 0 18px 0;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &.with-legend {
      margin-bottom: 40px;
    }
    &[disabled] {
      cursor: not-allowed;
      .rz-pointer {
        cursor: not-allowed;
        background-color: #d8e0f3;
      }
      .rz-draggable {
        cursor: not-allowed;
      }
      .rz-selection {
        background: #8b91a2;
      }
      .rz-tick {
        cursor: not-allowed;
      }
      .rz-selected {
        background: #8b91a2;
      }
    }
    span {
      position: absolute;
      display: inline-block;
      white-space: nowrap;
    }
    .rz-base {
      width: 100%;
      height: 100%;
      padding: 0;
    }
    .rz-bar-wrapper {
      left: 0;
      z-index: 1;
      width: 100%;
      height: 32px;
      padding-top: 16px;
      margin-top: -16px;
      box-sizing: border-box;
      .rz-bar {
        left: 0;
        z-index: 1;
        width: 100%;
        height: 3px;
        background: $grey;
        border-radius: 2px;
      }
    }
    .rz-draggable {
      cursor: move;
    }
    .rz-pointer {
      top: -16px;
      z-index: 3;
      width: 32px;
      height: 32px;
      cursor: pointer;
      background-color: $deep-warm;
      border-radius: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 12px;
        left: 12px;
        width: 8px;
        height: 8px;
        background: $off-white;
        border-radius: 4px;
      }
      &:hover:after {
        background-color: $white;
      }
      &.rz-active {
        z-index: 4;
        background-color: darken($dark-green, 4%);
        &:after {
          background-color: $soft-grey;
        }
      }
      &:focus {
        outline: 0;
      }
    }
    .rz-bubble {
      bottom: 24px;
      padding: 1px 3px;
      color: $off-white;
      background: $black;
      cursor: default;
      padding: 4px;
      border-radius: 6px;
      &.rz-limit {
        bottom: 32px;
        background: none;
        color: $dark-green;
      }
    }
    .rz-ticks {
      position: absolute;
      top: -3px;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 0;
      margin: 0;
      list-style: none;
      box-sizing: border-box;
      .rz-ticks-values-under .rz-tick-value {
        top: auto;
        bottom: -32px;
      }
      .rz-tick {
        position: absolute;
        top: -1px;
        left: 0;
        width: 1px;
        height: 10px;
        margin-left: 16px;
        text-align: center;
        cursor: pointer;
        background: lighten($deep-warm, 10);
        .rz-selected {
          background: $deep-warm;
        }
      }
      .rz-tick-value {
        position: absolute;
        top: -30px;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
      }
    }
  }
  // Charts
  .data-legend {
    margin: 12px 0;
    text-align: center;
    li {
      display: inline-block;
      padding: 4px 12px 4px 4px;
      &:last-child {
       margin-right: 0;
      }
    }
    span {
      &.legend {
        display: inline-block;
        width: 24px;
        height: 16px;
        margin-right: 4px;
      }
      strong {
        font-size: 16px;
        margin: 0 2px;
      }
    }
  }
  .extend-legend {
    padding: 8px;
    text-align: center;
    li {
      margin-bottom: 12px;
      h3 {
        font-size: 13px;
        margin-bottom: 4px;
      }
      span {
        font-size: 14px;
        strong {
          font-size: 18px;
        }
      }
      a[data-toggle='tooltip'] {
        background: url(#{$images-path}icons/info-icon.png) no-repeat center center;
        background-size: cover;
        height: 16px;
        width: 16px;
        display: inline-block;
        margin: 0 0 0 8px;
      }
    }
    > li {
      &:first-child {
        span {
          font-size: 18px;
          strong {
            font-size: 22px;
          }
        }
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    .data-group {
      border: solid 1px lighten($green, 20%);
      border-radius: 0 0 5px 5px;
      background-color: $off-white;
      li {
        float: left;
        width: 33%;
        span {
          display: block;
          padding: 8px 0;
          border-right: solid 1px $soft-grey;
        }
        &:last-child {
          width: 34%;
          span {
            border-right: none;
          }
        }

        h3 {
          padding: 4px 0;
          background-color: lighten($green, 40%);
          color: $dark-green;
          margin-bottom: 0px;
          min-height: 48px;
          text-align: center;
          font-size: 14px;
        }
      }
    }
    &.info {
      .data-group {
        background: none;
        li {
          margin-bottom: 0px;
          h3 {
            background: none;
            min-height: auto;
          }
        }
      }
    }
  }

  // Money
  .money-control {
    margin: 24px 0 24px;
    padding-bottom: 12px;
    border-bottom: solid 1px $grey;
    .money-number {
      color: $black;
      text-align: center;
      font-size: 18px;
      padding: 4px 4px 12px 4px;
      border-right: solid 1px $grey;
      &.last {
        border-right: none;
      }
      input {
        display: inline-block;
        width: auto;
        max-width: 140px;
        font-weight: 600;
        font-size: 28px;
        box-shadow: none;
        height: auto;
      }
      strong {
        font-size: 28px;
        padding: 6px 12px;
        display: inline-block;
        border: solid 1px transparent;
      }
      & + label {
        display: block;
        color: $grey;
        text-align: center;
        font-size: 13px;
      }
    }
  }
  .money-list {
    padding: 8px;
    li {
      border-bottom: solid 1px $grey;
      padding: 8px 0;
      font-size: 13px;
      div {
        float: left;
        width: calc(100% - 120px);
      }
      span {
        float: right;
        margin: 12px 12px 0 0;
        font-size: 20px;
        strong {
          font-size: 24px;
        }
      }
    }
  }
