/* Forms
================================================== */
// Default Form

.form-group {
  margin-bottom: 15px;
  &.focus{
    label{
      color: $soft-grey;
    }
  }
  label {
    color:$grey;
    font-weight: 400;
    font-size: 12px;
    padding-left: 3px;
    &.required:before {
      content: '*';
      position: relative;
      margin-right: 8px;
      display: inline;
      font-size: 12px;
    }
  }
  .helper-text{
    font-size: 12px;
  }
  &.error{
    .helper-text{
      color: $input-color-error;
    }
    input,
    textarea,
    select {
      &.form-control{
        border: 1px solid $input-color-error;
      }
    }
  }
  input,
  textarea,
  select {
    &.form-control{
      outline:none;
      background-color: $input-bg;
      line-height: 1;
      color: $dark-grey;
      border: 1px solid $input-bdr-color;
      border-radius: 4px;
      padding: 8px 12px;
      width: 100%;
      // Clean Form
      &:focus {
        border-color: $input-focus;
        background: $input-bg;
        box-shadow: $input-shadow-focus;
      }
      &::-webkit-input-placeholder { 
        color: $light-grey;
        opacity: 1;
      }
      &::-moz-placeholder { 
        color: $light-grey;
        opacity: 1;
      }
      &:-ms-input-placeholder {
        color: $light-grey;
        opacity: 1;
      }
      &:-moz-placeholder {
        color: $light-grey;
        opacity: 1;
      }
    }
  }

  a {
    color: $cold;
    &.link {
      border-bottom: solid 1px $cold;
    }
  }
  &.password, &.back {
    text-align: right;
  }
  &.back {
    margin: 64px 0 24px;
    text-align: left;
    a {
      color: $dark-cold;
    }
  }
}

.check,.radio {
  input{
    position: relative;
    width: 0;
    height: 0;
  }
  label{
    position: relative;
    padding-left: 25px;
    height: 20px;
    &:before{
      left: 0;
      top:50%;
      margin-top: -10px;
      position: absolute;
      content:'';
      width: 20px;
      height: 20px;
      background: $white;    
    }

  }
}
.check {
  label:before{
    border-radius: 5px;
    border: solid 2px $deep-green;
    cursor: pointer;
  }
  input + label:after{
    content:' ';
    position: absolute;  
    // background-color: $deep-green;
    z-index: 1;
    width: 0;
    height: 0;   
    transition: height 100ms ease-in-out 50ms;
  }
  input:disabled + label:before{
    opacity: 0.5;
    cursor:inherit;
  }
  input:checked + label:after{
    -moz-transform: scaleX(-1) rotate(138deg);
    -ms-transform: scaleX(-1) rotate(138deg);
    -webkit-transform: scaleX(-1) rotate(138deg);
    transform: scaleX(-1) rotate(138deg);
    -moz-transform-origin: left top;
    -ms-transform-origin: left top;
    -webkit-transform-origin: left top;
    transform-origin: left top;
    border-right: 3px solid $deep-green;
    border-top: 3px solid $deep-green;
    content: '';
    display: block;
    left: 4px;
    position: absolute;
    top: 8px;
    height: 9px;   
    width: 7px;
  }
}

.radio {
  label{
    z-index: 1;
  }
  label:before{
    border-radius: 50%;
    border: solid 2px $deep-green;
    cursor: pointer;
  }
  input + label:after{
    content:' ';
    position: absolute;  
    border-radius: 50%;
    background-color:$deep-green;
    z-index: 1;
    width: 0;
    height: 0;
    transition: height 100ms ease-in-out 50ms;
  }
  input:disabled + label:before{
    opacity: 0.5;
    cursor:inherit;
  }

  input:checked + label:after{
    content: '';
    display: block;
    position: absolute;
    left: 4px;
    top: 2px;
    margin-top: 0;
    margin-left: 0;
    width: 12px;
    height: 12px;
  }
}

.form-inline {
  font-size: 0;
  margin-right: -10px;
  .form-group{
    display: inline-block;
    vertical-align: top;
  }

  @for $i from 1 to 12 {
    .col-#{$i} { 
      width: 100%;
      @include breakpoint($tablet) {
        padding-right: 10px;
        width:50%;
      }     
      @include breakpoint($desktop) {
        padding-right: 10px;
        width:percentage(1/$i);
      }     
    }
  }
}
