/* Typography
================================================== */
  // Headings
  h1, h2, h3, h4, h5 {
    font-size-adjust: 0.5;
    text-rendering: optimizeLegibility;
    margin: 0 0 8px 0;
  }
  h1 {
    @extend .ff2;
    font-size: 2.5em;
    font-weight: 600;
    line-height: 1.4;
  }
  h2 {
    @extend .ff2;
    font-weight: 300;
    font-size: 2.5em;
    line-height: 1.4;
  }
  h3 {
    font-size: 1.8em;
  }
  h4 {
    font-size: 1.125em;
    font-family: $ff-secondary;
  }

  h1, h2, h3, h4 {
    a {
      font-weight:inherit;
    }
  }

  h5 {
    font-family: $ff-primary;
  }
  a {
    color: $dark-grey;
    text-decoration: none;
    &:visited {
      text-decoration: none;
    }
  }
  blockquote {
    font-size: 1.5em;
    line-height: 1.458em;
    @include breakpoint($tablet) {
      font-size: 1.125em;
      font-size: 1.25em;
    }
  }
p{
  font-family: $ff-primary;
  font-weight: 400;
}
.ff2{
  font-family: $ff-secondary;
}
.ff1{
  font-family: $ff-primary;
}
.fwl{
  font-weight: 300;
}
.fwr{
  font-weight: 400;
}
.fwb{
  font-weight: 600;
}
.lb{
  font-size: 1.125em;
  
}
.dib{
  display: inline-block;
}