/* Variables
================================================== */
// Column Variables
$column:                60px;
$gutter:                20px;


// Static Grid Breakpoint Variables (Media Queries)
$ext-desktop:           1200px;
$desktop:               992px;
$tablet:                768px;
$ext-mobile:            360px;
$mobile:                320px;

$desktop-l:             1440px;
$tablet-p:              640px (orientation portrait);
$tablet-l:              800px (orientation landscape);


//Assets location
$fonts-path:            '../fonts' !default;
$images-path:           '../img/';


// Font Variables
$openSansRegular:       'open_sansregular';
$openSansItalic:        'open_sansitalic';
$openSansLight:         'open_sanslight';
$openSansSemibold:      'open_sanssemibold';
$arial:                 'Arial', 'Helvetica', sans-serif;
$fif-prefix:            'fif-';

$ff-primary:        	'Source Sans Pro', sans-serif;
$ff-secondary:          'Raleway', sans-serif;

$body-font-size: 14px; 
$base-line-height: 1.6;


//-->BASE COLORS

//warm
$deep-warm:              #c840e9;
$dark-warm:              #d65eaf;
$warm:                   #e47d75;
$soft-warm:              #f19b3a;
$light-warm:             #ffb900;

//cold
$deep-cold:             #665eff;
$dark-cold:             #5b7af8;
$cold:                  #5095f0;
$soft-cold:             #45b1e9;
$light-cold:            #3acce1;

// green
$deep-green:            #29af3b;
$dark-green:            #5cc26a;
$green:                 #8fd599;
$soft-green:            #c2e8c7;
$light-green:           #def8e2;


// Greyscale
$black:                 #000000;

$dark-grey:             #1e1e1e;
$grey:                  #555555;
$soft-grey:             #8c8c8c;
$light-grey:            #c2c2c2;
$off-white:             #f9f9f9;

$white:                 #ffffff;


// Forms
$input-focus:           $deep-cold;
$input-bg:              #ffffff;
$input-bdr-color:         $soft-grey;
$input-bdr-color-focus:   $deep-cold;
$input-shadow:            0 0  0 rgba(0, 0, 0, 0.16);
$input-shadow-focus:      0 2px 3px 0 rgba(0, 0, 0, 0.16);
$input-ph:                $light-grey;
$input-ph-focus:          $dark-grey;              
$input-color-error:     #ff4f9a;


// Alerts & Error
$info-green:            #62a642;
$info-blue:             #0080ff;
$info-red:              #ff0000;
$info-orange:           #fff2de;
$info-tangerine:        #F7931E;
$info-brown:            #A67C52;


// Shadows
$text-shadow:           2px 2px 4px rgba($black, .8);
$box-shadow:            0px 3px 4px rgba($black, .15);
$box-shadow-error:      inset -0px 2px 4px rgba($soft-grey, 1);
$box-shadow-focus:      0 0 5px rgba($input-focus, 1);


// Transitions
$transition-property: all;
$transition-time: .3s;
$method: ease-in-out;
@mixin transitionButton() {
  //-ms-transition: $transition-property $transition-time $method;
  //transition: $transition-property $transition-time $method;
  transition: color .25s linear, background-color .5s ease-in-out, border-color .25s ease-in;
}
@mixin transitionHeader() {
  transition: position .85s ease-in, background .75s ease-in-out, padding 1.15s linear;
}
