/* Fonts
================================================== */
  // Vars
  [data-icon]:before {
    font-family: "fundary" !important;
    content: attr(data-icon);
    font-style: normal !important;
    font-weight: normal !important;
    font-variant: normal !important;
    text-transform: none !important;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  [class^="fif-"]:before,
  [class*=" fif-"]:before {
    font-family: "fundary" !important;
    font-style: normal !important;
    font-weight: normal !important;
    font-variant: normal !important;
    text-transform: none !important;
    font-size: 16px;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  // Menu
  .#{$fif-prefix}home:before {
    content: "\e000";
  }
  .#{$fif-prefix}originator:before {
    content: "\e001";
  }
  .#{$fif-prefix}ambassador:before {
    content: "\e002";
  }
  .#{$fif-prefix}borrower:before {
    content: "\e003";
  }
  .#{$fif-prefix}credit:before {
    content: "\e004";
  }
  .#{$fif-prefix}profile:before {
    content: "\e005";
  }
  .#{$fif-prefix}loans:before {
    content: "\e006";
  }
  .#{$fif-prefix}payment:before {
    content: "\e007";
  }


