/* Header
================================================== */
  // Base



  .dataTables_wrapper {
    .dataTables_filter {
      padding: 8px;
      background-color: $white;
      label {
        width: 100%;
        display: block;
        overflow: hidden;
        text-align: center;
        font-weight: 400;
      }
      input {
        width: calc(100% - 60px);
        margin-left: 4px;
        font-size: 16px;
        line-height: 22px;
        height: 40px;
        color: $black;
        border: 1px solid lighten($grey, 15);
        border-radius: 3px;
        padding: 6px 30px 6px 12px;
      }

    }
    .table>tbody>tr>td {
      vertical-align: middle;
      img {
        height: 36px;
        width: 36px;
      }
    }
    @include breakpoint($tablet) {
      .dataTables_filter {
        box-shadow: none;
        label {
          text-align: right;
        }
        input {
          width: auto;
        }
      }
    }
  }
