/* Header
================================================== */
  // Base
  .navbar-custom {
    background: $white;
    box-shadow: $box-shadow;
    z-index: 120;
    position: relative;
    margin-bottom: 0;
    border-radius: 0;
    .navbar-header {
      .navbar-brand {
        padding: 12px 15px;
        width: 48px;
        overflow: hidden;
        .logo-text {
          display: none;
          visibility: hidden;
        }
      }
      .navbar-toggle {
        display: none;
        margin-left: 0;
        padding: 9px 0;
        .icon-bar {
          background-color: $black;
          width: 24px;
          &.last {
            width: 12px;
          }
        }
      }
      .navbar-links {
        float: right;
        margin-top: 4px;
        a {
          padding: 12px 15px;
          display: inline-block;
        }
      }
    }
    .nav > li {
      & a.active,
      & a:active {
        color: $green;
      }
      & a:focus,
      & a:hover {
        background-color: lighten($green, 25);
        color: $grey !important;
        border-radius: 3px;
      }
      &.open {
        & > a,
        & a:focus,
        & a:hover {
          background-color: lighten($green, 35);
        }
      }
    }
    .dropdown-menu > li > a {
      padding: 8px 20px;
    }
    // Registered
    &.register {
      background: $green;
      .navbar-header {
        .navbar-brand {
          display: none;
          .logo-text {
            fill: $off-white;
          }
        }
        .navbar-toggle .icon-bar {
          background-color: $off-white;
          width: 20px;
          &.last {
            width: 12px;
          }
        }
      }
      .navbar-collapse.collapse {
        float: right;
        padding-right: 0;
        ul {
          margin: 0;
        }
        .navbar-nav li {
          display: inline-block;
        }
      }
      .navbar-nav {
        & > li {
          & > a {
            color: $off-white;
            display: inline-block;
            padding: 10px 15px;
            .caret {
              margin: 0 4px;
            }
            i {
              margin-left: 4px;
            }
          }
          &.user {
            border-left: solid 1px $green;
          }
        }
      }
    }

    @include breakpoint($tablet) {
      overflow: visible;
      padding: 18px 10px 9px;
      .navbar-header {
        padding-left: 15px;
        .navbar-brand {
          width: auto;
          .logo-text {
            display: block;
            visibility: visible;
          }
        }
      }
      .navbar-links {
        display: none;
      }
      .navbar-nav {
        float: right;
      }
      .nav > li  a {
        color: $white;
        color: $grey;
      }
      &.register {
        .navbar-header {
          .navbar-brand {
            display: block;
          }
        }
      }
      &.fixed-header {
        padding: 4px 10px;
        position: fixed;
        width: 100%;
      }
    }
    @include breakpoint($desktop-l) {
      .container-fluid {
        margin: 0 auto;
        width: 1440px;
      }
    }
  }
  .wrapper-landing {
    .navbar {
      // Index
      background: none;
      width: 100%;
      box-shadow: none;
      .nav > li  a {
        margin-top: 12px;
        border: 2px solid $white;
        border-radius: 20px;
        padding: 6px 28px;
      }
      &.fixed-header {
        background: linear-gradient(0deg, rgba($white, 1), rgba($white, 0.9));
        padding: 4px 10px;
        box-shadow: $box-shadow;
        .nav > li  a {
          margin-top: 8px;
          border: 2px solid $green;
          color: $dark-green;
        }
      }
    }
    @include breakpoint($tablet) {
      .navbar {
        position: fixed;
      }
    }
  }
/* Footer
================================================== */
  // Headings
  #footer {
    color: $off-white;
    display: none;
    width: 100%;
    position: fixed;
    bottom: 0;
    z-index: 100;
    .container-fluid {
      background-color: $black;
      padding: 0 16px;
    }
    a, span {
      color: $off-white;
      padding: 0 12px;
    }
    @include breakpoint($tablet) {
      display: block;
      p {
        font-size: 12px;
        line-height: 36px;
        margin: 0 0 0 8px;
        display: inline-block;
      }
      .social {
        list-style: none;
        margin: 0;
        text-align: center;
        li {
          margin: 0 4px;
          display: inline-block;
          a {
            display: block;
            height: 36px;
            width: 36px;
            padding: 0;
            i {
              margin-top: 0;
              height: 36px;
              display: inline-block;
              opacity: .6;
            }
            .icon {
              fill: rgba($soft-grey, .8);
            }
            &:hover {
              background: darken($black, 5);
              .icon {
                fill: $soft-grey;
              }
            }
          }
          &.phone a {
            width: auto;
            span {
              color: $white;
              font-size: 12px;
              margin: 6px 0 0 0;
              float: right;
              padding: 0;
            }
          }
        }
      }
      .legals {
        text-align: center;
      }
    }
    @include breakpoint($ext-desktop) {
      .container-fluid {
        .social {
          text-align: left;
        }
        .legals {
          text-align: right;
        }
      }
    }
    @include breakpoint($desktop-l) {
      .container-fluid {
        margin: 0 auto;
        width: 1440px;
      }
    }
  }

