/* Button
================================================== */
  // Core
  @mixin btn-disabled {
    border: none;
    cursor: default;
    box-shadow: none;
    text-shadow: none;
    opacity: 0.33;
  }
  @mixin btn-core {
    font-family: $openSansRegular;
    outline: 0;
    word-spacing: .06em;
  }
  @mixin btn-style ($bgcolor, $bdrcolor, $txtcolor) {
    background-color: $bgcolor;
    border: solid 1px $bdrcolor;
    color: $txtcolor !important;
  }


  // Styles
  @mixin btn {
    @include btn-core;
    @include transitionButton();
  }
  @mixin btn-default {
    @include btn-core;
    @include btn-style($white, $black, darken($black, 10));
    padding: 8px 36px;
    border-width: 1px;

    &:active, &:focus, &:hover {
      background-color: $white;
      border-color: $grey;
      color: $black !important;
    }
    &:active {
      &:hover {
        background-color: $off-white;
        border-color: lighten($grey, 24);
      }
    }
  }
  @mixin btn-primary {
    @include btn-core;
    @include btn-style($deep-green, $deep-green, $white);
    padding-left: 48px;
    padding-right: 48px;
    min-width: 180px;
    &:active, &:focus, &:hover {
      background-color: lighten($warm, 12);
   
      color: $white;
    }
    &:active {
      &:hover {
        background-color: darken($warm, 12);
        border-color: lighten($warm, 24);
      }
    }
  }
  @mixin btn-secondary {
    @include btn-core;
    @include btn-style($warm, $warm, $black);
    padding-left: 48px;
    padding-right: 48px;
    &:active, &:focus, &:hover {
      background-color: lighten($warm, 12);
      border-color: $warm;
      color: $white;
    }
    &:active {
      &:hover {
        background-color: darken($warm, 12);
        border-color: lighten($warm, 24);
      }
    }
  }
  @mixin btn-green {
    @include btn-core;
    @include btn-style($white, $green, darken($green, 10));
    padding: 8px 36px;
    border-width: 1px;

    &:active, &:focus, &:hover {
      background-color: $white;
      border-color: $dark-green;
      color: $dark-green!important;
    }
    &:active {
      &:hover {
        background-color: $off-white;
        border-color: lighten($green, 24);
      }
    }
  }

  @mixin btn-warm {
    @include btn-core;
    @include btn-style($white, $warm, darken($warm, 10));
    padding: 8px 36px;
    border-width: 1px;

    &:active, &:focus, &:hover {
      background-color: $white;
      border-color: $dark-warm;
      color: $black !important;
    }
    &:active {
      &:hover {
        background-color: $off-white;
        border-color: lighten($soft-warm, 24);
      }
    }
  }

  input[type='submit'] { @include btn-primary; }
  .btn, button, input[type='button'], input[type='reset'] {
    @include btn;
  }

  .btn-default {
    @include btn-default;
    border-radius: 5px;
    &.disabled,
    &[disabled] {
      @include btn-disabled;
    }
  }
  .btn-primary {
    @include btn-primary;
    border-radius: 24px;
    &.disabled,
    &[disabled] {
      @include btn-disabled;
    }
  }
  .btn-secondary {
    @include btn-secondary;
    &.disabled,
    &[disabled] {
      @include btn-disabled;
    }
  }
  .btn-green {
    @include btn-green;
    border-radius: 5px;
    &.disabled,
    &[disabled] {
      @include btn-disabled;
    }
  }
  .btn-warm {
    @include btn-warm;
    border-radius: 5px;
    &.disabled,
    &[disabled] {
      @include btn-disabled;
    }
  }

  // Xtras
  .btn-sm {
    padding: 5px 10px;
    font-size: 12px;
    line-height: 1.5;
    border-radius: 3px;
  }
  .btn-rounded {
    border-radius: 24px;
  }
